.payment-button {
    background: #000;
    color: #fff;
    width: 240px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.5rem;
    border: none;
    font-size: 1rem;
    border-bottom-right-radius: 25px;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 0px;
    cursor: pointer;
    margin-top: 2.5rem;
    transition: 0.5s;
    margin-right: 0;
    margin-left: auto;
}

.payment-button:hover {
    border-bottom-left-radius: 0px;
    border-top-right-radius: 25px;
}

h1 {
    margin-left: 10px;
    margin-top: 100px;
    margin-bottom: 50px;
}