select {
  -webkit-appearance: none;
          appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  outline: none;
}

:root {
  --select-border: #777;
  --select-focus: blue;
  --select-arrow: var(--select-border);
}

.option-list-div {
  /* width: 70%; */
  /* margin-right: 0; */
  min-width: 20px;
  border: 1px solid #777;
  border: 1px solid var(--select-border);
  border-radius: 1em;
  padding: 0.25em 0.5em;
  font-size: 0.9rem;
  cursor: pointer;
  line-height: 1.0;
  background-color: #fff;
  display: grid;
  grid-template-areas: "select";
  -webkit-align-items: center;
          align-items: center;
}

select,
.option-list-div:after {
  grid-area: select;
}

.option-list-div:after {
  content: "";
  width: 0.8em;
  height: 0.5em;
  background-color: #777;
  background-color: var(--select-arrow);
  -webkit-clip-path: polygon(100% 0%, 0 0%, 50% 100%);
          clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  justify-self: end;
}

